 /* Button */

.norseu-button-min-size {
	min-height: 35px;
	min-width: 90px;
}

.norseu-button-min-size-icon-only {
	min-height: 35px;
	min-width: 35px;
}

.norseu-button {
	/*margin: 5px;*/
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	display: inline-flex;
	cursor: pointer;
	-webkit-user-select: none;
	        user-select: none;
	align-items: center;
	vertical-align: bottom;
	text-align: center;
	overflow: hidden;
	position: relative;
	background-color: #F5F8FA;
	color: #293742;
	padding: 10px;
	border-radius: 5px;
	border: 1px solid #A7B6C2;
	text-decoration: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    /* min-width: fit-content; */
}

.norseu-button-icon {
    
}

.norseu-button-rounded-border {
    border-radius: 20px;
}

.norseu-button-raised-border {
    border: 1px solid #A7B6C2;
    box-shadow: 1px 2.5px 5px #A7B6C2;
    -webkit-box-shadow: 1px 2.5px 5px #A7B6C2;
    -moz-box-shadow: 1px 2.5px 5px #A7B6C2;
}

.norseu-button:hover {
    -webkit-filter: brightness(97%);
            filter: brightness(97%);
}

.norseu-button:active {
    box-shadow: inset 0px 0px 400px 110px rgba(0, 0, 0, .1);
}

.norseu-button:focus {
    z-index: 9;
    outline: none;
    box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
    -webkit-box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
    -moz-box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
}

/* ButtonGroup */

.norseu-button-group {
    display: flex;
    padding: 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.norseu-button-group > .norseu-button {
    margin: 0px;
    border-radius: 0px;
}

.norseu-button-group > .norseu-button:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.norseu-button-group > .norseu-button:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.norseu-button-group.norseu-flex-vertical {
    flex-wrap: wrap;
}

.norseu-button-group.norseu-flex-vertical > .norseu-button {
    border-radius: 0px;
}

.norseu-button-group.norseu-flex-vertical > .norseu-button:first-child {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.norseu-button-group.norseu-flex-vertical > .norseu-button:last-child {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}


