/* The loading Class */

span.norseu-skeleton {
	display: inline-block;
	max-height: 25px;
}

.norseu-skeleton { 
	position: relative; 
	color: #e4e4e4 !important;
	background-color: #e4e4e4 !important;
	pointer-events: none;
	border: none !important;
	overflow: hidden !important;
	-webkit-touch-callout: none; /* iOS Safari */
	  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */ /* Internet Explorer/Edge */
			  user-select: none; /* Non-prefixed version, currently
									supported by Chrome, Edge, Opera and Firefox */
}  

.norseu-skeleton > * {
	display: none !important;
}

.norseu-skeleton::-webkit-input-placeholder {
	color: transparent;
}

.norseu-skeleton::placeholder {
	color: transparent;
}

.norseu-skeleton::after { 
	display: block; 
	content: ""; 
	position: absolute; 
	width: 100%; 
	height: 100%; 
	top: 0px; 
	overflow: hidden;
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%); 
				  
	background: linear-gradient(90deg, transparent, 
			rgba(255, 255, 255, 0.5), transparent); 

	-webkit-animation: norseu-skeleton 1.0s infinite; 

	        animation: norseu-skeleton 1.0s infinite; 
} 

@-webkit-keyframes norseu-skeleton { 
	100% { 
		-webkit-transform: translateX(100%); 
		        transform: translateX(100%); 
	} 
} 

@keyframes norseu-skeleton { 
	100% { 
		-webkit-transform: translateX(100%); 
		        transform: translateX(100%); 
	} 
}

/* End The loading Class */ 

