.norseu-floating-label {
	position: relative;
	margin: 40px 0 20px;
}

.norseu-floating-label > label {
	font-weight: normal;
	position: absolute;
	pointer-events: none;
	left: 8px;
	top: 0%;
	line-height: 32px;
	transition: 0.2s ease all;
	-moz-transition: 0.2s ease all;
	-webkit-transition: 0.2s ease all;
}

.norseu-floating-label > .norseu-inputtext-label-flushed {
