    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.norseu-toast-position-center {
	left: 50%;
	top: 50%;
    min-width: 20vw;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.norseu-toast-message-enter {
    opacity: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}

.norseu-toast-message-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: opacity .3s, -webkit-transform .3s;
    transition: transform .3s, opacity .3s;
    transition: transform .3s, opacity .3s, -webkit-transform .3s;
}

.norseu-toast-message-enter-done {
    -webkit-transform: none;
            transform: none;
}

.norseu-toast-message-exit {
    opacity: 1;
    max-height: 1000px;
}

.norseu-toast-message-exit-active {
    opacity: 0;
    max-height: 0;
    margin-bottom: 0;
    overflow: hidden;
    transition: max-height .45s cubic-bezier(0, 1, 0, 1), opacity .3s, margin-bottom .3s;
}

